import Prime from "../assets/Img/OptimusPrime_Comprim.png";
import Bee from "../assets/Img/Bumblebee_Comprim.png";
import Sound from "../assets/Img/Soundwave_Comprim.png";
import Jet from "../assets/Img/Jetfire_Comprim.png";
import Grim from "../assets/Img/Grimlock_Comprim.png";
// import video from "../assets/Video/TransformersPresentacion.mp4";
import Tanque from "../assets/Video/Tanque2_Comprim.mp4";
import Infiltradores from "../assets/Video/Infiltradores_Comprim.mp4";
import Cientificos from "../assets/Video/Cientificos_Comprim.mp4";
import Medicos from "../assets/Video/Medicos_Comprim.mp4";
import Luchadores from "../assets/Video/Luchadores_Comprim.mp4";
import mg from "../assets/logos/Mage_icon.png";
import tank from "../assets/logos/Tank_icon.png";
import jg from "../assets/logos/Jungle_icon.png";
import supp from "../assets/logos/Support_icon.png";
import fig from "../assets/logos/Fighter_icon.png";
import BGAuto from "../assets/logos/AutoBg.png";
import BGDecep from "../assets/logos/DecepBg.png";
import BGDino from "../assets/logos/DinoBg.png";
import { usePjClass } from "../hooks/usePjClass";
import { useEffect, useState } from "react";
const ChoseYourClassDesktop = () => {
  let [pj, handleChange] = usePjClass({
    name: "OPTIMUS PRIME",
    info: "Lider de los Autobots",
    type: "1",
  });

  let [bg, setBg] = useState({
    backgroundImage: `url(${BGAuto})`,
    backgroundPosition: "bottom",
    backgroundSize: "cover",
  });
  useEffect(() => {
    if (pj.type === "1") {
      setBg({
        backgroundImage: `url(${BGAuto})`,
        backgroundPosition: "bottom",
        backgroundSize: "cover",
      });
    } else if (pj.type === "2") {
      setBg({
        backgroundImage: `url(${BGDecep})`,
        backgroundPosition: "bottom",
        backgroundSize: "contain",
        backgroundRepeat: "no-repeat",
      });
    } else if (pj.type === "3") {
      setBg({
        backgroundImage: `url(${BGDino})`,
        backgroundPosition: "top",
        backgroundSize: "contain",
        backgroundRepeat: "no-repeat",
      });
    }

    return () => {};
  }, [pj]);
  return (
    <div className="div-container-desktop-flex">
      <div className="div-container-height">
        <div className="div-container-buttons-pj">
          <div className="div-container-icon-champs">
            <label className="label-active" htmlFor="TANQUES">
              <img src={tank} alt="" />
            </label>
            <label htmlFor="INFILTRADORES">
              <img src={jg} alt="" />
            </label>
            <label htmlFor="CIENTIFICOS">
              <img src={mg} alt="" />
            </label>
            <label htmlFor="MEDICOS">
              <img src={supp} alt="" />
            </label>

            <label htmlFor="LUCHADORES">
              <img src={fig} alt="" />
            </label>
          </div>
          <form>
            <input
              type="submit"
              name="OPTIMUS PRIME"
              className="input-active"
              data-information="Lider de los Autobots"
              data-number="0"
              data-type="1"
              data-src={Tanque}
              value="TANQUES"
              id="TANQUES"
              onClick={handleChange}
            />
            <input
              type="submit"
              name="BUMBLEBEE"
              data-information="Autobot de infiltración"
              data-number="1"
              data-type="1"
              data-src={Infiltradores}
              value="INFILTRADORES"
              id="INFILTRADORES"
              onClick={handleChange}
            />
            <input
              type="submit"
              name="SOUNDWAVE"
              data-information="Decepticon de ciencia"
              data-number="2"
              data-type="2"
              data-src={Cientificos}
              value="CIENTÍFICOS"
              id="CIENTIFICOS"
              onClick={handleChange}
            />
            <input
              type="submit"
              name="RATCHET"
              data-information="Autobot Médico"
              data-number="3"
              data-type="1"
              data-src={Medicos}
              value="MÉDICOS"
              id="MEDICOS"
              onClick={handleChange}
            />
            <input
              type="submit"
              name="GRIMLOCK"
              data-information="Lider de los Dinobots"
              data-number="4"
              data-type="3"
              data-src={Luchadores}
              value="LUCHADORES"
              id="LUCHADORES"
              onClick={handleChange}
            />
          </form>

          <div className="container-video-choose-class">
            <figure className="figure-container-video-class">
              <video
                className="video-choose-class video-active-class"
                autoPlay
                muted
                loop
                preload="metadata"
                src={Tanque}
              ></video>
              <video
                className="video-choose-class"
                muted
                loop
                src={Infiltradores}
              ></video>

              <video
                className="video-choose-class"
                muted
                loop
                preload="metadata"
                src={Cientificos}
              ></video>

              <video
                className="video-choose-class"
                muted
                loop
                preload="metadata"
                src={Medicos}
              ></video>

              <video
                className="video-choose-class"
                muted
                loop
                preload="metadata"
                src={Luchadores}
              ></video>
            </figure>
          </div>
        </div>
      </div>

      <div className="characters">
        <div className="div-border">
          <figure>
            <img className="img-active" src={Prime} alt="" />
            <img src={Bee} alt="" />
            <img src={Sound} alt="" />
            <img src={Jet} alt="" />
            <img src={Grim} alt="" />
          </figure>
        </div>

        <article className="information-pj">
          <h3>{pj.name}</h3>
          <p>{pj.info}</p>
        </article>
        <div className="container-img-bg" style={bg}></div>
      </div>
    </div>
  );
};

export default ChoseYourClassDesktop;
