import "./ChoseYourClass.css";
// import BGAuto from "../assets/logos/AutoBg.png";
// import BGDecep from "../assets/logos/DecepBg.png";
// import BGDino from "../assets/logos/DinoBg.png";
// import { usePjClass } from "../hooks/usePjClass";
import { useEffect, useState } from "react";
import ChoseYourClassDesktop from "./ChoseYourClassDesktop";
import { useResizeWindow } from "../hooks/useResizeWindow";
import ChoseYourClassMobile from "./ChoseYourClassMobile";
const ChoseYourClass = () => {
  const [mq, WindowRisezeHash, DomLoadedHash, hashChangeWindow] =
    useResizeWindow("(min-width: 1024px)");
  let [classVisibility, setClassVisibilite] = useState(null);
  // let [pj, handleChange] = usePjClass({
  //   name: "OPTIMUS PRIME",
  //   info: "Lider de los Autobots",
  //   type: "1",
  // });

  // let [bg, setBg] = useState({
  //   backgroundImage: `url(${BGAuto})`,
  //   backgroundPosition: "bottom",
  //   backgroundSize: "cover",
  // });

  // useEffect(() => {
  //   if (pj.type === "1") {
  //     setBg({
  //       backgroundImage: `url(${BGAuto})`,
  //       backgroundPosition: "bottom",
  //       backgroundSize: "cover",
  //     });
  //   } else if (pj.type === "2") {
  //     setBg({
  //       backgroundImage: `url(${BGDecep})`,
  //       backgroundPosition: "bottom",
  //       backgroundSize: "contain",
  //       backgroundRepeat: "no-repeat",
  //     });
  //   } else if (pj.type === "3") {
  //     setBg({
  //       backgroundImage: `url(${BGDino})`,
  //       backgroundPosition: "top",
  //       backgroundSize: "contain",
  //       backgroundRepeat: "no-repeat",
  //     });
  //   }

  //   return () => {};
  // }, [pj]);

  let controller = new AbortController();
  let signal = controller.signal;
  useEffect(() => {
    if (mq === null) return;
    const ChoseYourClassFunctionMq = async () => {
      if (mq) {
        setClassVisibilite(<ChoseYourClassDesktop />);
      } else {
        setClassVisibilite(<ChoseYourClassMobile />);
      }
    };

    Promise.all([ChoseYourClassMobile], { signal }).then(() =>
      ChoseYourClassFunctionMq()
    );

    return () => {
      controller.abort();
      window.removeEventListener("resize", WindowRisezeHash);
      document.removeEventListener("DOMContentLoaded", DomLoadedHash);
      window.removeEventListener("hashchange", hashChangeWindow);
      setClassVisibilite(null);
    };
  }, [mq]);

  return (
    <section className="section-class-pj">
      <article className="article-class-title">
        <h3>ELIGE TU</h3>
        <h2>CLASE</h2>
      </article>
      <article className="article-class-information">
        <p>
          Ya sea que prefieras lanzarte directo a la batalla, apoyar a tus
          compañeros de equipo, o algo intermedio, hay un lugar para ti en la
          Cybertrón.
        </p>
      </article>
      <div className="div-two-buttons">
        <button className="button-more-transformers">
          DESCUBRIR MÁS TRANSFORMERS
        </button>
        <button className="button-visit-now">VISITAR AHORA</button>
      </div>
      {classVisibility}
    </section>
  );
};

export default ChoseYourClass;
