import Linkedin from "../assets/redes/icons8-linkedin-50.png";
import GitHub from "../assets/redes/icons8-github-50.png";
import Twitter from "../assets/redes/icons8-twitter-50.png";
import "./SocialNetwork.css"
const SocialNetwork = () => {
  return (
    <section className="social-network-section">
      <div className="container-network-div">
        <article>
          <h2>REDES SOCIALES</h2>
          <div className="networks">
            <a href="as">
              <img src={Linkedin} alt="" />
            </a>

            <a href="as">
              <img src={GitHub} alt="" />
            </a>

            <a href="as">
              <img src={Twitter} alt="" />
            </a>
          </div>
        </article>
      </div>
    </section>
  );
};

export default SocialNetwork;
