import "./ToFindOut.css";
import Bee from "../assets/Img/transformersSiege1.jpg";
import Bee2 from "../assets/Img/transformersSiege2.jpg";
import { useEffect } from "react";
import { canvasHelper } from "./helpers/canvasHelper";
const ToFindOut = () => {
  useEffect(() => {
    canvasHelper(".border-aa", "0.5rem");
    const $divContainerCard = document.querySelector(
      ".div-container-img-find-out"
    );

    let observer = new IntersectionObserver(
      (e) => {
        if (e[0].isIntersecting) {
          function animate() {
            requestAnimationFrame(animate);
          }
          animate();

          // $divContainerCard.classList.add("active-translate");
          $divContainerCard.style.transform = `translateY(${0}%)`;
        } else {
          if (e[0].boundingClientRect.y > 0) {
            $divContainerCard.classList.remove("active-translate");

            function animate2() {
              // requestAnimationFrame(animate2)
              $divContainerCard.style.transform = `translateY(${50}%)`;
            }
            animate2();
          } else {
          }
        }
      },
      { threshold: 0.5 }
    );

    let target = document.querySelector(".section-find-out-bg");
    observer.observe(target);

    return () => (observer = null);
  }, []);
  return (
    <section className="section-find-out-bg">
      <article className="article-find-out">
        <h3>MÁS PARA</h3>
        <h2>DESCUBRIR</h2>
      </article>

      <div className="div-container-img-find-out">
        <a href="sd">
          <article>
            <figure>
              <img src={Bee} alt="" />
            </figure>
            <div className="div-container-title-article">
              <h3>ESTRUCTURA COMPETITIVA 2020</h3>
            </div>
          </article>
          <div className="div-border-a-find-out">
            <canvas className="border-aa"></canvas>
          </div>
        </a>

        <a href="sd" className="second-a">
          <article>
            <figure>
              <img src={Bee2} alt="" />
            </figure>
            <div className="div-container-title-article">
              <h3>ESTRUCTURA COMPETITIVA 2021</h3>
            </div>
          </article>
          <div className="div-border-a-find-out">
            <canvas className="border-aa"></canvas>
          </div>
        </a>
      </div>
    </section>
  );
};

export default ToFindOut;
